.BreadBottom {
    height: 13%;
    width: 65%;
    background: linear-gradient(#F08E4A, #e27b36);
    border-radius: 0 0 30px 30px;
    box-shadow: inset -15px 0 #c15711;
    margin: 2% auto;
}

.BreadTop {
    height: 20%;
    width: 65%;
    background: linear-gradient(#bc581e, #e27b36);
    border-radius: 50% 50% 0 0;
    box-shadow: inset -15px 0 #c15711;
    margin: 2% auto;
    position: relative;
}

.Seeds1 {
    width: 10%;
    height: 15%;
    position: absolute;
    background-color: white;
    left: 30%;
    top: 50%;
    border-radius: 40%;
    transform: rotate(-20deg);
    box-shadow: inset -2px -3px #c9c9c9;
}

.Seeds1:after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: white;
    left: -170%;
    top: -260%;
    border-radius: 40%;
    transform: rotate(60deg);
    box-shadow: inset -1px 2px #c9c9c9;
}

.Seeds1:before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: white;
    left: 180%;
    top: -50%;
    border-radius: 40%;
    transform: rotate(60deg);
    box-shadow: inset -1px -3px #c9c9c9;
}

.Seeds2 {
    width: 10%;
    height: 15%;
    position: absolute;
    background-color: white;
    left: 64%;
    top: 50%;
    border-radius: 40%;
    transform: rotate(10deg);
    box-shadow: inset -3px 0 #c9c9c9;
}

.Seeds2:before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: white;
    left: 150%;
    top: -130%;
    border-radius: 40%;
    transform: rotate(90deg);
    box-shadow: inset 1px 3px #c9c9c9;
}

.Meat, .Cheese, .Bacon, .Salad, .Chicken, .Tomato, .Cucumber, .Onion  {
    position: relative;
}

.Onion {
    width: 65%;
    height: 5%;
    margin: 2% auto;
    background: linear-gradient(#ac56c8, #8d58a8);
    border-radius: 10px;
}

.Cucumber {
    width: 72%;
    height: 5%;
    margin: 2% auto;
    background: linear-gradient(#09ab0e, #09870e);
    border-radius: 10px;
}

.Tomato {
    width: 72%;
    height: 5%;
    margin: 2% auto;
    background: linear-gradient(#b82f1d, #8c261b);
    border-radius: 10px;
}

.Chicken {
    width: 70%;
    height: 8%;
    background: linear-gradient(#7f5910, #684210);
    margin: 2% auto;
    border-radius: 15px;
}

.Meat {
    width: 70%;
    height: 8%;
    background: linear-gradient(#7f3608, #702e05);
    margin: 2% auto;
    border-radius: 15px;
}

.Cheese {
    width: 75%;
    height: 5.5%;
    margin: 2% auto;
    background: linear-gradient(#f4d004, #d6bb22);
    border-radius: 20px;
}

.Salad {
    width: 75%;
    height: 7%;
    margin: 2% auto;
    background: linear-gradient(#228c1d, #91ce50);
    border-radius: 20px;
}

.Bacon {
    width: 65%;
    height: 5%;
    background: linear-gradient(#bf3813, #c45e38);
    margin: 2% auto;
}

.RemoveButton {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%) translateX(100%);
}

.IngredientName {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    color: white;
    text-align: center;
    width: 100%;
    height: 100%;
}